@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow: hidden; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
body {
  -ms-overflow-style: none;
}

.homepage {
  color: black;
  background-image: url(/saketVerma/static/media/BG.ae384ac8.png);
  /* height: 100%; */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  color: black;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-content: center;
  /* height: 100%; */
  padding-top: 200px;
}

.containerMain {
  padding-top: 80px;
  padding-bottom: 100px;
}

.navbar-container {
  width: 100%;
  display: flex;
  justify-content: center;
}


.navbar {
  /* background-color: transparent; */
  font-size: 25px;
  display: flex;
  justify-content: space-between !important;
  /* padding-bottom: 10px; */
  /* opacity: 0.7; */
  /* max-width: 875px; */
  width: 100%;
  background-color: white;
  /* margin-bottom: 10px; */
  padding: 5px;
}

.container-inner {
  background-color: white;
  padding: 20px;
  padding-bottom: 50px;
  /* height: 800px; */
  width: 100%;
  max-width: 800px;
  min-width: 290px;
}

.container-and-sidebar {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.sidebar {
  background-color: white;
  width: 75px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: self-start;
  padding-top: 20px;
}

.sidebar-container {
  display: flex;
  flex-direction: column;
  /* padding-left: 10px; */
}

.sidebar-bottom-triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 25px 75px 0 0;
  border-color: #ffffff transparent transparent transparent;
}

.personal {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 30px 10px;
  background-color: #c0e3e7;
  display: flex;
  justify-content: center;
  align-items: center;
}

.personal-inner {
  display: flex;
  width: 80%;
  height: 70%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center;
}

.personal-info {
  align-self: center;
}

.subtitle {
  color: #7d7d7d;
}

.avatar {
  width: 200px;
  border-radius: 50%;
  margin: 15px 0px;
}

.shadows {
  box-shadow: 0px 5px 5px lightgrey;
}

.social-media {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;
}

.fa {
  font-size: 25px;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: black !important;
}

.avatar-mini {
  width: 40px;
  border-radius: 50%;
}

.aboutMe {
  padding-top: 30px;
}

.font-bold {
  font-weight: 500;
}

.center-text {
  text-align: center;
  display: flex;
  justify-content: center;
}

.padding-top-down {
  padding-top: 10px;
  padding-bottom: 10px;
}

.heading-sub {
  font-size: 30px;
}

.inner-content {
  padding: 0px 20px;
}

@media only screen and (min-width: 300px) {
  
  /* FOR MOBILE DEVICES */
  
  .tabletPlus {
    display: none;
  }
  .mobile-only {
    display: block;
  }
  .mobile-center {
    text-align: center;
  }
  .h1,
  h1 {
    font-size: 32px !important;
  }

  .h5,
  h5 {
    font-size: 18px !important;
  }

  .notOnMobile {
    display: none;
  }

}

@media only screen and (min-width: 768px) {
  
  /* FOR TABLET AND ABOVE */

  .tabletPlus {
    display: flex;
  }

  .mobile-only {
    display: none;
  }

  .mobile-center {
    text-align: left;
  }
  .h1,
  h1 {
    font-size: 40px !important;
  }

  .h5,
  h5 {
    font-size: 20px !important;
  }

  .notOnMobile {
    display: block;
  }
}

.verticalLine {
    padding-top: 20px;
    height: inherit;
    width: 5px;
    border-left: 3px solid #C0E3E7;
}

.Education-Container {
    padding-top: 30px;
    width: 100%;
}

.row {
    flex-wrap: nowrap !important;
    /* padding-top: 20px; */
    margin-left: 0px !important;
    margin-right: 0px !important;
}

.leftSide {
    /* flex-wrap: wrap; */
    width: 40%;
    text-align: right;
}

.rightSide {
    /* flex-wrap: wrap; */
    width: 40%;
